.graphical-editor-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
}

.action-node {
  border: 1px solid var(--panel-border);
  border-radius: 5px;
  background-color: var(--panel-back);
  padding: 10px;
}
.action-node .node-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.action-node .node-header .node-title {
  font-weight: bold;
}
.action-node .node-header .node-controls button {
  margin-left: 5px;
}
.action-node .node-content {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.action-node .node-content .field {
  display: flex;
  align-items: center;
  gap: 10px;
}
.action-node .node-content .field label {
  width: 100px;
  text-align: right;
  flex-shrink: 0;
}
.action-node .node-content .field input[type=text],
.action-node .node-content .field input[type=number],
.action-node .node-content .field select {
  flex-grow: 1;
  width: 100%;
  padding: 5px 10px;
  border: 1px solid var(--panel-border);
  border-radius: 4px;
  background-color: #333;
  color: var(--txt);
}
.action-node .node-content .field input[type=text]:focus,
.action-node .node-content .field input[type=number]:focus,
.action-node .node-content .field select:focus {
  outline: none;
  border-color: var(--accent);
}
.action-node .node-content .field input[type=checkbox] {
  width: auto;
}

.node-btn,
.add-action-btn,
.add-choice-btn {
  padding: 5px 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}
.node-btn:hover,
.add-action-btn:hover,
.add-choice-btn:hover {
  background-color: #0056b3;
}
.node-btn:disabled,
.add-action-btn:disabled,
.add-choice-btn:disabled {
  background-color: #555;
  cursor: not-allowed;
}

.delete-btn {
  background-color: #dc3545;
}
.delete-btn:hover {
  background-color: #c82333;
}

.add-action-btn {
  align-self: flex-start;
}

.add-choice-btn {
  align-self: flex-start;
  margin-top: 10px;
}

.choices-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 10px;
}

.choice-container {
  border: 1px solid #444;
  padding: 10px;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.choice-fields {
  display: flex;
  flex-direction: column;
  gap: 8px;
  flex-grow: 1;
}