.scene-manager-content {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.scene-manager-content #scene-list {
  list-style: none;
  padding: 0;
  margin: 0;
  overflow-y: auto;
  flex-grow: 1;
}
.scene-manager-content #scene-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  border-bottom: 1px solid #ccc;
}
.scene-manager-content #scene-list li:last-child {
  border-bottom: none;
}
.scene-manager-content .scene-item-controls {
  display: flex;
  gap: 4px;
}
.scene-manager-content .scene-manager-controls {
  display: flex;
  padding: 8px;
  border-top: 1px solid #ccc;
}
.scene-manager-content .scene-manager-controls input {
  flex-grow: 1;
  margin-right: 8px;
}