#game-scene {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: relative;
}
#game-scene #background {
  flex: 1;
  max-height: calc(100% - 120px);
  object-fit: cover;
  border-radius: 10px;
}
#game-scene #dialog-box {
  width: 100%;
  height: 100px;
  background-color: #111;
  color: #fff;
  overflow: hidden;
  border-radius: 10px;
  padding: 10px;
  margin-top: 10px;
}
#game-scene #choices-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(100% - 120px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
  pointer-events: none;
}
#game-scene #choices-container button {
  pointer-events: all;
  padding: 10px 20px;
  background-color: #333;
  color: white;
  border: 1px solid #555;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  min-width: 200px;
  text-align: center;
  margin: 5px 0;
}
#game-scene #choices-container button:hover {
  background-color: #555;
}